import SosGetResultAudio from "osh-js/core/datasource/SosGetResultAudio";
import {DATASOURCE_DATA_TOPIC} from "osh-js/core/Constants";

const button = document.querySelector('button');
const samplesElement = document.getElementById("samples");

button.onclick = () => test7();

let audioDecoder = new AudioDecoder({
    output: onDecodedAudio,
    error: onDecodedAudioError
});

let AudioContext = window.AudioContext || window.webkitAudioContext;
let audioCtx = new AudioContext();
let deltaInc = 0;

function onDecodedAudio(decodedSample) {
    const buffer = decodedSample.buffer;
    let source = audioCtx.createBufferSource();
    source.buffer = buffer;
    source.connect(audioCtx.destination);
    source.start(deltaInc);
    deltaInc += buffer.duration;
}

function onDecodedAudioError(error) {
    console.error(error);
}

const sampleRate = 11025;

async function init() {
    await audioDecoder.configure({
        codec: 'mp4a.40.2',
        numberOfChannels:1,
        sampleRate: sampleRate
    });
}

let count =0;
async function test6() {
    await init();
    fetch('./data/ff-16b-1c-8000hz.aac')
        // Retrieve its body as ReadableStream
        .then(response => {
            const stream = response.body;
            const reader = stream.getReader();

            let first =  true;
            let deltaInc = 0;
            reader.read().then(async function processBytes({done, value}) {
                if (done) {
                    console.log("Stream complete");
                    return;
                }
                deltaInc = 0;

                const chunk = new EncodedAudioChunk({
                    type:  first? "key" : "delta",
                    data: value.buffer,
                    timestamp: 0
                });

                try {
                    audioDecoder.decode(chunk);
                    console.log(value.length/sampleRate, count);
                } catch (error) {
                    console.error(error, (value.length/sampleRate),count);
                }

                count++;
                deltaInc += value.length/sampleRate;
                first = false;
                return reader.read().then(processBytes);
            });
        });
}


async function test7() {
    console.log('using webcodec');
    // create data source for UAV camera
    let audioDataSource = new SosGetResultAudio("alex-audio", {
        protocol: "ws",
        service: "SOS",
        endpointUrl: "sensiasoft.net:8181/sensorhub/sos",
        offeringID: "urn:android:device:dd90fceba7fd5b47-sos",
        observedProperty: "http://sensorml.com/ont/swe/property/AudioFrame",
        startTime: "2021-03-24T09:56:00Z",
        endTime: "now",
        // replaySpeed: 1.5,
        batchSize:500
    });

    const bc  = new BroadcastChannel(DATASOURCE_DATA_TOPIC + audioDataSource.id);

    let first = true;
    await init();
    bc.onmessage = async (message) => {
        if (message.data.type === 'data') {
            for (let value of message.data.values) {
                const data = value.data.frameData;
                const chunk = new EncodedAudioChunk({
                    type:  first? "key" : "delta",
                    data: data.buffer,
                    timestamp: 0
                });

                try {
                    audioDecoder.decode(chunk);
                } catch (error) {
                    console.error(error, (value.length/sampleRate),count);
                }

                // first = false;
            }
        }
    }

    audioDataSource.connect();
}
